<template>
   <v-container fill-height justify-center fluid v-if="!검색함">
     <h1>썸썸체크</h1>
     <v-row>
       <v-col>
         <v-text-field label="이름" v-model="이름"/>
       </v-col>
     </v-row>
    <v-btn @click="checkSome(이름)">저장</v-btn>
  </v-container>
  <v-container fill-height justify-center fluid v-else>
     <h1>찾은목록</h1>
     <v-row>
        <v-col cols="12">
            <v-list height="400">
                <v-subheader>
                    {{이름}}으로 {{검색완료}}명의 썸💘 후보가 검색되었습니다.
                </v-subheader>
                <v-list-item three-line v-for="(item, index) in 찾은목록" :key="`finded-${index}`" @click="openDetails(item)">
                    <v-list-item-content >
                        <v-list-item-title>{{item.닉네임}}</v-list-item-title>
                        <v-list-item-subtitle>{{item.닉네임}}</v-list-item-subtitle>
                        <v-list-item-subtitle v-if="item.썸타겟">나를 검색한적이 있어요!</v-list-item-subtitle>
                    </v-list-item-content>
                </v-list-item>
            </v-list>
        </v-col>
     </v-row>
    <v-btn @click="checkSome(이름)">저장</v-btn>
    <UserDetails v-if="modal.userDetails" :userDetails="selectedDetails" @close="close"/>
  </v-container>
</template>

<script>
import * as firestore from "firebase/firestore"
export default {
    name: 'Dashboard',
    components:{
        UserDetails:()=>import("@/components/user-details")
    },
    data(){
      return{
        이름:"",
        찾은목록:[],
        modal:{
            userDetails:false
        },
        selectedDetails:null,
        검색함:false
      }
    },
    computed:{
        검색완료(){
            return this.찾은목록.length
        }
    },
    watch:{
    },
    methods:{
      async checkSome(이름){
        try {
            const uid = this.$currentUser.uid
            const docRef = firestore.doc(this.$db, "사용자", uid);
            const docSnap = await firestore.getDoc(docRef);
            let myName = null
            if (docSnap.exists()) {
                myName = docSnap.data().이름
            } else {
                console.log("No such document!");
            }
            const getTarget = firestore.query(firestore.collection(this.$db, "사용자"), firestore.where("이름", "==", 이름));
            const querySnapshot = await firestore.getDocs(getTarget);
            const arr = []
            await querySnapshot.forEach((doc) => {
                arr.push({...doc.data(),id:doc.id});
            });
            this.찾은목록 = arr
            const 찾음 = arr.length
            if(찾음) this.findHistory(arr, myName)
            this.검색함 = true
            this.createHistory(이름, 찾음)
        } catch (e) {
            console.error(e)
        }
      },
      async findHistory(arr, myName){
            const idArr = await arr.map(item=>item.id)
            const getTarget = firestore.query(firestore.collection(this.$db, "검색이력"), firestore.where("대상자이름", "==", myName), firestore.where("검색자", "in", idArr));
            const querySnapshot = await firestore.getDocs(getTarget)
            const res = []
            await querySnapshot.forEach((doc) => {
                res.push({...doc.data(),id:doc.id});
            });

            this.찾은목록 = this.찾은목록.map((item)=>{
                item.썸타겟 = res.some(it=>it.검색자 == item.id)
                return item
            })
      },
      async createHistory(이름, 찾음){
          const uid = this.$currentUser.uid
          const payload = {
            검색일: new Date(),
            검색자: uid,
            대상자이름:이름,
            찾음여부: 찾음
          }
          const ref = firestore.collection(this.$db, "검색이력")
          const id = await firestore.addDoc(ref, payload);
          console.log(id)
      },
      async createAccount(이름){
        try {
          const uid = this.$currentUser.uid
          const payload = {
            이름:이름,
            가입일: new Date(),
            // 나이:29,
            마지막접속: new Date(),
            // 성별: "남",
            // 지역: "성남시",
          }

          const ref = firestore.collection(this.$db, "사용자")
          await firestore.setDoc(firestore.doc(ref,uid), payload);
          this.$router.push("/dashboard")
        } catch (e) {
          console.error("Error adding document: ", e);
        }
      },
      openDetails(details){
          this.selectedDetails = details
          this.modal.userDetails = true
      },
      close(){
          Object.keys(this.modal).forEach(k=>this.modal[k] = false)
      }
    }

}
</script>

